'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Search, Github } from 'lucide-react'
import Image from 'next/image'
import Link from 'next/link'

export default function HomePage() {
  const [username, setUsername] = useState('')
  const [isLoading, setIsLoading] = useState(false)

  const handleSearch = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!username || !username.includes('#')) {
      return
    }
    
    setIsLoading(true)
    // Redirection vers la page du joueur
    window.location.href = `/player?username=${encodeURIComponent(username)}`
  }

  return (
    <div className="min-h-screen bg-slate-900">
      {/* Navigation */}
      <nav className="flex items-center justify-between p-6">
        <div className="flex items-center space-x-6">
          <Link href="/" className="flex items-center space-x-2">
            <Image
              src="/images/logo.png"
              alt="ZhonyaS Logo"
              width={40}
              height={40}
              className="rounded-lg"
            />
            <span className="text-2xl font-bold text-white">ZhonyaS</span>
          </Link>
          <Link 
            href="https://github.com/YuToutCourt/ZhonyaS" 
            target="_blank"
            className="flex items-center space-x-2 text-gray-300 hover:text-white transition-colors"
          >
            <Github className="w-5 h-5" />
            <span>GitHub</span>
          </Link>
        </div>
        
        {/* Search bar in nav */}
        <form onSubmit={handleSearch} className="flex items-center space-x-2">
          <Input
            type="text"
            placeholder="Username#Tag..."
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="w-64 bg-white/10 border-white/20 text-white placeholder:text-gray-300"
            required
          />
          <Button type="submit" disabled={isLoading} className="bg-blue-600 hover:bg-blue-700">
            <Search className="w-4 h-4" />
          </Button>
        </form>
      </nav>

      {/* Hero Section */}
      <div className="flex flex-col lg:flex-row items-center justify-center px-6 py-20 gap-12">
        {/* Image Section */}
        <div className="flex-1 max-w-lg">
          <div className="relative bg-slate-800/30 rounded-2xl p-4 backdrop-blur-sm">
            <Image
              src="/images/BIG_BRAUM.png"
              alt="League of Legends Illustration - Braum"
              width={600}
              height={600}
              className="w-full h-auto drop-shadow-2xl hover:scale-105 transition-transform duration-300 brightness-110 contrast-110"
              priority
            />
          </div>
        </div>

        {/* Content Section */}
        <div className="flex-1 max-w-2xl">
          <div className="text-center lg:text-left">
            <h1 className="text-6xl font-bold text-white mb-6">
              <span className="bg-gradient-to-r from-blue-400 to-yellow-400 bg-clip-text text-transparent">
                ZhonyaS
              </span>
            </h1>
            <p className="text-xl text-gray-300 leading-relaxed mb-8">
              Have you always wanted a tool to quickly scout a player before a scrim, tournament, or Clash?
              ZhonyaS is here for you. Using Riot Games' API, it retrieves all games of any player from June 2021 to the present. And get a summary of the player!
            </p>
          </div>
        </div>
      </div>

      {/* Search Section */}
      <div className="flex justify-center px-6 pb-20">
        {/* Main Search Card */}
        <Card className="w-full max-w-2xl bg-white/10 backdrop-blur-sm border-white/20">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl text-white">Search Player</CardTitle>
            <CardDescription className="text-gray-300">
              Enter a player's username and tag to analyze their performance
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSearch} className="space-y-4">
              <div className="flex space-x-2">
                <Input
                  type="text"
                  placeholder="Username#Tag..."
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  className="flex-1 bg-white/10 border-white/20 text-white placeholder:text-gray-300"
                  required
                />
                <Button 
                  type="submit" 
                  disabled={isLoading}
                  className="bg-blue-600 hover:bg-blue-700 px-8"
                >
                  {isLoading ? (
                    <div className="flex items-center space-x-2">
                      <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
                      <span>Searching...</span>
                    </div>
                  ) : (
                    <div className="flex items-center space-x-2">
                      <Search className="w-4 h-4" />
                      <span>Search</span>
                    </div>
                  )}
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>

    </div>
  )
}